import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';

@Component({
  providers: [Beyond],
  selector: 'app-list-estimates',
  templateUrl: './list-estimates.component.html',
  styleUrls: ['./list-estimates.component.scss']
})
export class ListEstimatesComponent implements OnInit {
  loginData: any;
  estimateListDetails: any;
  estimateListResponse: boolean;

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    console.log(this.loginData.loginCode)
    this.getListOfEstimates();
  }


  getStaffRole(selectRole) {
    //console.log(selectRole)
    if (selectRole == 'Staff') {
      this.getListOfEstimates();
    } else {
      this.getListOfManagerEstimates()
    }
  }


  addEstimate() {
    this.router.navigate(['/staff/createEstimateNew'])
  }

  getListOfEstimates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfProEstimates' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        console.log("estimateListDetails" + JSON.stringify(response))
        if (response.status == 200) {
          this.estimateListDetails = response.response;
          this.estimateListResponse = true;
        }
        else {
          this.estimateListResponse = false;
        }
        error => {
        }
      })
  }


  getListOfManagerEstimates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfManagerEstimates' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.estimateListDetails = response.response;
          //console.log("estimateListDetails" + JSON.stringify(this.estimateListDetails))
          this.estimateListResponse = true;
        }
        else {
          this.estimateListResponse = false;
        }
        error => {
        }
      })
  }

  viewEstimate(estimateNumber) {
    this.router.navigate(['/estimate/viewEstimate', estimateNumber])
  }

  editEstimate(estimateNumber) {
    this.router.navigate(['/estimate/editEstimate', estimateNumber])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
