  jobRelatedcost() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listPBJobrelated/' + this.billingAddressData.proId, options)
      .subscribe(data => {
        var equipAgreement = data.json();
        if (equipAgreement.status == 200) {
          //console.log("agreementStatus Details" + JSON.stringify(equipAgreement))
          this.jobrelated = equipAgreement.response;
          //console.log("list" + JSON.stringify(this.jobrelated))
          for (var i = 0; i < this.jobrelated.length; i++) {
            if (this.jobrelated[i].settingDesc == "Credit Card Processing Fee") {
              this.ccpf = this.jobrelated[i].settingValue
            } else if (this.jobrelated[i].settingDesc == "Truck Operating Cost") {
              this.toc = this.jobrelated[i].settingValue
            } else if (this.jobrelated[i].settingDesc == "Workmans Comp Insurance") {
              this.workmanCompIns = this.jobrelated[i].settingValue
            } else if (this.jobrelated[i].settingDesc == "Employee Tax Company") {
              this.empTaxComp = this.jobrelated[i].settingValue
            }
          }
          // return false;
          this.job = true
          //console.log("job" + this.job)
        } else {
          this.job = false
        }
      }, error => {
        //  this.toastr.warning('Unable to connect to server');
      })
  }

