import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddEstimateComponent } from './estimates/add-estimate/add-estimate.component';
import { EditEstimateComponent } from './estimates/edit-estimate/edit-estimate.component';
import { ViewEstimateComponent } from './estimates/view-estimate/view-estimate.component';
import { ListEstimatesComponent } from './estimates/list-estimates/list-estimates.component';
import { AddEstimate1Component } from './estimates/add-estimate1/add-estimate1.component';
import { ProzEstimateLayoutComponent } from './proz-estimate-layout/proz-estimate-layout.component';
const routes: Routes = [
  {
    path: '', component: ProzEstimateLayoutComponent,
    children: [
      /* -- Estimates -- */
      {
        path: 'addEstimate',
        component: AddEstimateComponent
      },
      {
        path: 'editEstimate/:estimateNumber',
        component: EditEstimateComponent
      },
      {
        path: 'viewEstimate/:estimateNumber',
        component: ViewEstimateComponent
      },
      {
        path: 'listEstimates',
        component: ListEstimatesComponent
      },
      {
        path: 'addEstimate1/:workOrderNumber',
        component: AddEstimate1Component
      },
      /* -- Estimates -- */

    ]

  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProzEstimateRoutingModule { }
