  //Estimate Analyser Service
  viewEstimateAnalyserService() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getEstimateAnalyserDetails/' + this.estimateNum, options)
      .subscribe(data => {
        var response = data.json();
        //console.log("ESTAnresponse" + JSON.stringify(response))
        if (response.status == 200) {
          this.estimateAnalyser = response.response[0]
          this.totalValueAfterDiscounts = this.estimateAnalyser.estGrossSale
          this.desiredProfitValues = this.estimateAnalyser.desiredProfitValue
          this.desiredProfitPercent = this.estimateAnalyser.desiredProfitPercent
          console.log("  this.desiredProfitValues" + this.desiredProfitValues)
          console.log(" this.estimateAnalyser.desiredProfitValue" + this.estimateAnalyser.desiredProfitValue)
          // this.creditCardPF = this.estimateAnalyser.ccProcessingFeePercent
          this.paymentModeValue = this.estimateAnalyser.ccProcessingFeeValue
          this.toc = this.estimateAnalyser.toc
          // this.customMaterialListValue = this.estimateAnalyser.estMaterialCostMarkUp
          //this.customLaborListValue = this.estimateAnalyser.estLaborCostMarkUp
          //this.otherCost = this.estimateAnalyser.otherCostValue
          this.oCost = this.estimateAnalyser.otherCost
          console.log("oCost" + this.oCost)
          this.desiredPercentage = this.estimateAnalyser.directJobCostPercent
          this.directJobCostValue = this.estimateAnalyser.directJobCostValue
          console.log("directJobCostValue" + this.directJobCostValue)
          this.estimatedGrossProfitValue = this.estimateAnalyser.estGrossProfit
          this.overHeadCostValue = this.estimateAnalyser.overHeadCostPercent
          this.overHeadEstimateCostValue = this.estimateAnalyser.overHeadCostValue
          this.empCommissionType = this.estimateAnalyser.estCommissionType
          //this.customLaborListValue = this.estimateAnalyser.estTechCommPercent
          this.channelManagerCommValue = this.estimateAnalyser.estTechCommValue
          this.priceBookHoursValue = this.estimateAnalyser.priceBookHours
          this.customLaborFieldTechHours = this.estimateAnalyser.fieldTechHours
          this.sumOfPriceBookCustomLaborHours = this.estimateAnalyser.totalHours
          this.workingType = this.estimateAnalyser.workingTypeValue
          this.commissionType = this.estimateAnalyser.estTechPayType
          this.totalHoursWithWorkingType = this.estimateAnalyser.estTechPay
          this.worksmanCompType = this.estimateAnalyser.worksmanCompType
          this.workmanCompIns = this.estimateAnalyser.employeeWorkmanCompValue
          this.workManEstComm = this.estimateAnalyser.employeeWorkmanCompPercent
          this.empTaxCompType = this.estimateAnalyser.empTaxCompType
          this.empTaxComp = this.estimateAnalyser.employeeTaxCompanyValue
          this.employeeTaxComm = this.estimateAnalyser.employeeTaxCompanyPercent
          this.employeeCommTaxInc = this.estimateAnalyser.employeeCommissions
          this.totalMarkupValue = this.estimateAnalyser.estProfitMaterialMarkup
          this.netProfitLaborWithCurrency = this.estimateAnalyser.estNetProfitBeforeTechCommission
          this.loginStaffCommissionType = this.estimateAnalyser.estNetTechCommissionType
          this.loginStaffCommission = this.estimateAnalyser.estTechCommPercent
          this.estTechCommWithCurrency = this.estimateAnalyser.estTechCommValue
          this.totalEstTechPay = this.estimateAnalyser.estTotalTechPay
          this.empCommissionType = this.estimateAnalyser.estCommissionType
          this.empCommission = this.estimateAnalyser.estTechCommPercent
          this.channelManagerCommValueNetProfit = this.estimateAnalyser.estTechCommValue
          this.estNetProfitWithCurrency = this.estimateAnalyser.estNetProfit
          this.estNetProfitPercentWithCurrency = this.estimateAnalyser.estNetProfitPercentage
          this.estimateAnalyserViewResponse = true;
          //console.log("empCommission" + this.empCommission)
        }
        else {
          this.estimateAnalyserViewResponse = false;
        }
        error => {
        }
      })
  }
