  getLoginStaffDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getStaffNameDetails/' + this.estimateBasicView.staffId, options)
      .subscribe(data => {
        var response = data.json();
        ////console.log("in staff channel manager")
        if (response.status == 200) {
          this.loginStaffDetailsList = response.response[0];
          this.loginStaffCommission = this.loginStaffDetailsList.compValue
          this.loginStaffCommissionType = this.loginStaffDetailsList.compType
          this.loginStaffGroup = this.loginStaffDetailsList.empgroup;
          this.loginStaffManagerId = this.loginStaffDetailsList.managerId
          if (this.loginStaffDetailsList.hourlyRate == "true") {
            this.commissionType = "HOURLY"
            this.workingType = this.loginStaffDetailsList.hourlyRateValue
            //console.log("working type" + this.workingType)
          } else if (this.loginStaffDetailsList.weeklyRate == "true") {
            this.commissionType = "WEEKLY"
            this.weeklyType = this.loginStaffDetailsList.weeklyRateValue
            this.workingType = this.weeklyType / 40;
          }
          this.loginStaffDetailsResponse = true;
          //this.listGroupDetails();
        }
        else {
          this.loginStaffDetailsResponse = false;
        }
        error => {
          ////console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

