import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import * as Moment from 'moment'
@Component({
  providers: [Beyond],
  selector: 'app-view-estimate',
  templateUrl: './view-estimate.component.html',
  styleUrls: ['./view-estimate.component.scss']
})
export class ViewEstimateComponent implements OnInit {
  estimateNum: any;
  loginData: any;
  estimateViewResponse: boolean;
  estimateBasicView: any;
  estimateCustomLaborData: any;
  estimateCustomMaterialData: any;
  estimatePriceBookData: any;
  totalEstimateCostData: any;
  estimateAnalyser: any;
  totalarrays: any;
  customMLDesc: any;
  finalArray: any;
  estimateAnalyserViewResponse: boolean = false;
  matchedStatus: boolean;
  billingAddressData: any;
  cusResponseStatus: boolean;
  year: number;
  month: number;
  day: number;
  hours: number;
  minutes: number;
  seconds: number;
  fullDateTime: string;
  enable: boolean;
  paymentType: any;//Chandu

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.estimateNum = this.activatedRoute.snapshot.params['estimateNumber']
    //console.log(this.estimateNum)
    this.viewEstimateService()
    this.viewEstimateAnalyserService()

  }

  viewEstimateService() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/estimateView/' + this.estimateNum, options)
      .subscribe(data => {
        var response = data.json();
        //console.log(response)
        if (response.status == 200) {
          this.estimateBasicView = response.response[0]
          //console.log("estimateBasicView"+JSON.stringify(this.estimateBasicView))
          this.estimateCustomLaborData = response.customLabourDetails
          this.estimateCustomMaterialData = response.customMaterialDetails
          this.estimatePriceBookData = response.pricebookDetails
          this.totalEstimateCostData = response.totalCostDetails[0]
          this.customMLDesc = response.customDescriptionDetails
          // var newArray = this.estimateCustomLaborData.concat(this.estimatePriceBookData);
          // this.totalarrays= newArray.concat(this.estimateCustomMaterialData);
          this.finalArray = this.estimatePriceBookData.concat(this.customMLDesc)
          // console.log("totalarrays" + JSON.stringify(this.totalarrays))
          console.log("finalArray" + JSON.stringify(this.finalArray))
          this.checkingStaffIdentity()
          this.customerDetailsData()
          this.enableGenerateInvoice()
          this.estimateViewResponse = true;
        }
        else {
          this.estimateViewResponse = false;
        }
        error => {
        }
      })
  }


  viewEstimateAnalyserService() {
    //console.log(this.estimateNum)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getEstimateAnalyserDetails/' + this.estimateNum, options)
      .subscribe(data => {
        var response = data.json();
        //console.log("response"+ JSON.stringify(response))
        if (response.status == 200) {
          this.estimateAnalyser = response.response[0]
          this.estimateAnalyserViewResponse = true;
        }
        else {
          this.estimateAnalyserViewResponse = false;
        }
        error => {
        }
      })
  }

  checkingStaffIdentity() {
    //console.log(this.loginData.loginCode)
    //console.log(this.estimateBasicView.managerId)
    if (this.loginData.loginCode == this.estimateBasicView.managerId) {
      this.matchedStatus = true;
      //console.log("matched")
    } else {
      this.matchedStatus = false;
      //console.log("Not matched")
    }
  }

  backEstimate() {
    this.router.navigate(['/estimate/listEstimates'])
  }


  customerDetailsData() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewStaffWorkOrder/' + this.estimateBasicView.workOrderNo, options)
      .subscribe(data => {
        var woResponse = data.json();
        if (woResponse.status == 200) {
          this.billingAddressData = woResponse.billingAddress[0]
          //console.log("billingAddressData"+JSON.stringify(this.billingAddressData))    
          this.cusResponseStatus = true;
        } else {
          this.cusResponseStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }

  approveEstimate() {

    let obj = {
      "estimateNum": this.estimateNum,
      "customerId": this.billingAddressData.customerId,
      "workOrderNum": this.billingAddressData.workOrderNumber,
      "customerName": this.billingAddressData.firstName + "-" + this.billingAddressData.lastName,
      "customerMail": this.billingAddressData.emailId,
      "customerPhone": this.billingAddressData.phoneNumber,
      "url": 'http://topproz.com.s3-website-us-east-1.amazonaws.com/auth/login/0',
      "estimateStatus": "PENDING CUSTOMER APPROVAL",
      "modifiedBy": this.loginData.name,
      "modifiedOn": Moment().format("MM-DD-YYYY HH:mm:ss")
    }
    //console.log("Edit OBJ" + JSON.stringify(obj))
    this.http.post(Global.url + '/updateManagerApproveStatus', obj)
      .subscribe(data => {
        var woData = data.json();
        //console.log("woData" + JSON.stringify(woData))
        this.router.navigate(['/estimate/listEstimates'])
      }, error => {
        //console.log(error);// Error getting the data
      });
  }

  editEstimate(estimateNumber) {
    this.router.navigate(['/estimate/editEstimate', estimateNumber])
  }

  enableGenerateInvoice() {
    if (this.estimateBasicView.estimateStatus == 'APPROVED') {
      this.enable = true;
    } else {
      this.enable = false;
    }
  }
  viewInvoice(estimateNo) {
    this.router.navigate(['/staff/generateInvoice', estimateNo])
  }

  //Chandu:22-07-2020
  paymentTypeSelection(x) {
    this.paymentType = x
    console.log("Payment Type = " + this.paymentType);
    //alert("Payment Type = "+this.paymentType)
  }
}
