  getEstimateNumber() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(Global.url + "/generateEstimateID", options)
      .subscribe(data => {
        this.estNumInfo = data.json();
        if (this.estNumInfo.status == 200) {
          this.estimateID = this.estNumInfo.estId;
          this.dataEstInfo = true;
        } else {
          this.dataEstInfo = false;
          ////console.log("NO Records Found");
        }
      });
  }
