  //CHARGE AMOUNT
  async chargeAmount() {
    this.spinner.show()
    const estimatePayment = await this.estimatePayment('/estimatesStripePayment', {
      amount: this.totalMaterialCostWithTax,
    })
    if (estimatePayment['succeeded'] == true) {
      this.spinner.hide()
      this.toastr.success('Estimates Payment Done Successfully')
    } else {
      this.spinner.hide()
      this.toastr.error('Estimates Payment Not successful')
    }
  }
  //CHARGE AMOUNT
  //STRIPE ESTIMATES PAYMENT
  estimatePayment(apiUrl, object) {
    return new Promise((resolve, reject) => {
      this.http.post(Global.url + apiUrl, object)
        .subscribe(data => {
          resolve(data.json())
        }, error => {
          resolve(error)
        })
    })
  }
